import React, { useEffect, useState, Suspense } from "react";
import Img from "react-cool-img";

function Ads(props) {
  const choice = [
    "Android App",
    "PDF Parser",
    "Data Visualize",
    "Data Augmentation",
    "Desktop Application",
  ];

  const [text, setText] = useState(choice[0]);

  useEffect(() => {
    setInterval(() => {
      const randint = Math.floor(Math.random() * choice.length);
      setText(choice[randint]);
    }, 2000);
  }, []);

  return (
    <div className="ads">
      <div className="text-white" style={{ fontSize: "50px" }}>
        Need a Personal Website for Business?
      </div>
      <div className="text-white" style={{ fontSize: "45px" }}>
        Want a custom
        <div
          className="change-cell"
          style={{ fontSize: "45px", display: "inline" }}
        >
          &nbsp;{text}&nbsp;
        </div>
        for your task?
      </div>
      <div className="text-white">
        Drop a message to <a href="">wizardlordlam@gmail.com</a> by clicking the
        link
      </div>
    </div>
  );
}

function Image(props) {
  return (
    <Suspense fallback="Loading">
      <div className="polaroid">
        <Img src={props.img} style={{ width: "100%" }} debounce={1000} />
        <div className="box">
          <p>{props.text}</p>
        </div>
      </div>
    </Suspense>
  );
}

function Hubcounter() {
  const [data, setData] = useState([]);

  useEffect(() => {
    const link = "https://api.github.com/users/HackerTon/repos";

    fetch(link)
      .then((res) => {
        return res.json();
      })
      .then((json) => {
        json.sort((a, b) => {
          if (a.updated_at < b.updated_at) {
            return 1;
          } else {
            return -1;
          }
        });

        setData(json);
      })
      .catch((e) => {
        console.error(e);
      });
  }, []);

  return (
    <div className="table">
      <table className="table table-hover">
        <thead className="thead-dark">
          <tr>
            <th scope="col">Name</th>
            <th scope="col">Username</th>
            <th scope="col">Description</th>
            <th scope="col">Last update</th>
          </tr>
        </thead>
        <tbody>
          {data.map((slot) => {
            const date = new Date(slot.updated_at);
            const year = date.getFullYear();

            return (
              <tr key={slot.id}>
                <th scope="row">
                  <a href={slot.html_url}>{slot.name}</a>
                </th>
                <td>{slot.owner.login}</td>
                <td>{slot.description}</td>
                <td>{year}</td>
              </tr>
            );
          })}
        </tbody>
      </table>
    </div>
  );
}

export { Hubcounter, Image, Ads };
